import TemperatureChart from "@/app/ui/user-dashboard/temperatureChart";

export default function SensorDetails({
  params,
  searchParams,
}: {
  params: { type: string };
  searchParams: { range?: string };
}) {
  const { type } = params;
  const range = searchParams.range || "week";

  let labels: string[] = [];
  let dataPoints: number[] = [];

  if (type.includes("temperature")) {
    if (range === "week") {
      labels = ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"];
      dataPoints = [24, 25, 23, 26, 28, 27, 24];
    } else if (range === "month") {
      labels = Array.from({ length: 30 }, (_, i) => `Day ${i + 1}`);
      dataPoints = Array.from({ length: 30 }, () => 20 + Math.random() * 10);
    }
  }

  return (
    <div className="min-h-screen bg-gray-50 p-4">
      <div className="max-w-5xl mx-auto bg-white p-6 rounded-xl shadow-md">
        <div className="mb-4 flex gap-2">
          <a
            href={`?range=week`}
            className={`px-4 py-1 rounded-lg ${
              range === "week" ? "bg-teal-500 text-white" : "bg-gray-200"
            }`}
          >
            1 هفته
          </a>
          <a
            href={`?range=month`}
            className={`px-4 py-1 rounded-lg ${
              range === "month" ? "bg-teal-500 text-white" : "bg-gray-200"
            }`}
          >
            1 ماه
          </a>
        </div>
        <TemperatureChart labels={labels} dataPoints={dataPoints} range={range} />
      </div>
    </div>
  );
}
